$(document).ready(function () {
  
    //Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    //Select
    $(function() {
        $(".select-hidden .select-item").click(function(e) {
            e.stopPropagation();
            let $item = $(this);
            let html = $item.html();
            $item.closest(".select").find(".select-current").html(html);
            $item.closest(".select").removeClass("select--active");
            $item.siblings(".select-item").removeClass("select-item--active");
            $item.addClass("select-item--active");
        });

        $(".select-show").hover(function() {
            let $sel = $(this).closest(".select");
            $(".select").not($sel).removeClass("select--active");
            $sel.addClass("select--active");
        });

        $(".select-current").on("click", function(e) {
            e.stopPropagation();
            let $sel = $(this).closest(".select");
            $(".select").not($sel).removeClass("select--active");
            $sel.toggleClass("select--active");
        });

        $(".select").on("mouseleave", function() {
            $(this).removeClass("select--active");
        });

        $(document).on("click", function(e) {
            if (!$(e.target).closest(".select").length) {
                $(".select").removeClass("select--active");
            }
        });
    });




})